html {
  line-height: 1.15;
  -webkit-text-size-adjust: 100%;
}
body {
  margin: 0;
}
main {
  display: block;
}
h1 {
  font-size: 2em;
  margin: 0.67em 0;
}
hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible;
}
pre {
  font-family: monospace, monospace;
  font-size: 1em;
}
a {
  background-color: transparent;
}
abbr[title] {
  border-bottom: none;
  text-decoration: underline;
  text-decoration: underline dotted;
}
b,
strong {
  font-weight: bolder;
}
code,
kbd,
samp {
  font-family: monospace, monospace;
  font-size: 1em;
}
small {
  font-size: 80%;
}
sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}
sub {
  bottom: -0.25em;
}
sup {
  top: -0.5em;
}
img {
  border-style: none;
}
button,
input,
optgroup,
select,
textarea {
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
  margin: 0;
}
button,
input {
  overflow: visible;
}
button,
select {
  text-transform: none;
}
button,
[type="button"],
[type="reset"],
[type="submit"] {
  -webkit-appearance: button;
}
button::-moz-focus-inner,
[type="button"]::-moz-focus-inner,
[type="reset"]::-moz-focus-inner,
[type="submit"]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}
button:-moz-focusring,
[type="button"]:-moz-focusring,
[type="reset"]:-moz-focusring,
[type="submit"]:-moz-focusring {
  outline: 1px dotted ButtonText;
}
fieldset {
  padding: 0.35em 0.75em 0.625em;
}
legend {
  box-sizing: border-box;
  color: inherit;
  display: table;
  max-width: 100%;
  padding: 0;
  white-space: normal;
}
progress {
  vertical-align: baseline;
}
textarea {
  overflow: auto;
}
[type="checkbox"],
[type="radio"] {
  box-sizing: border-box;
  padding: 0;
}
[type="number"]::-webkit-inner-spin-button,
[type="number"]::-webkit-outer-spin-button {
  height: auto;
}
[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}
[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}
::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}
details {
  display: block;
}
summary {
  display: list-item;
}
template {
  display: none;
}
[hidden] {
  display: none;
}
* {
  box-sizing: border-box;
}
body {
  min-width: 375px;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #000;
  background-color: #fff;
}
a {
  text-decoration: none;
}
img {
  max-width: 100%;
  height: auto;
}
.visually-hidden,
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.Container {
  max-width: 1400px;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
}
.flex-wrapper {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 40px;
}
@media (max-width: 1250px) {
  .flex-wrapper {
    flex-direction: column;
  }
}
.GoToTopButton {
  display: none;
  position: fixed;
  bottom: 20px;
  right: 30px;
  z-index: 99;
  border: none;
  outline: none;
  background-color: #ee6e73;
  color: #fff;
  cursor: pointer;
  padding: 15px;
  border-radius: 10px;
  font-size: 18px;
}
.GoToTopButton:hover {
  background-color: #555;
}
.Filters__list {
  padding: 0;
  margin: 30px 0;
  margin-bottom: 50px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-start;
  list-style: none;
}
@media (max-width: 900px) {
  .Filters__list {
    flex-direction: column;
  }
}
.select-group {
  position: relative;
  padding: 0;
  margin: 0;
  width: 17%;
}
.select-group::after {
  content: '\25BC';
  position: absolute;
  right: 0;
  bottom: 0;
  height: 48px;
  border-bottom: 1px solid #d0d0d0;
  border-right: 1px solid #d0d0d0;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  color: #000;
  padding-right: 10px;
  background-color: #fff;
  pointer-events: none;
  transition: 0.25s all ease;
}
.select-group:hover::after {
  color: #f39c12;
}
.select-group__select {
  appearance: none;
  outline: 0;
  box-shadow: none;
  border: 0;
  background-image: none;
  position: relative;
  display: block;
  width: 300px;
  height: 50px;
  line-height: 3;
  background: #fff;
  overflow: hidden;
  border: 1px solid #d0d0d0;
  width: 100%;
  height: 100%;
  margin: 0;
  margin-top: 10px;
  padding-left: 10px;
  color: #000;
  cursor: pointer;
}
.select-group__select::-ms-expand {
  display: none;
}
@media (max-width: 900px) {
  .select-group {
    width: 100%;
    margin-bottom: 20px;
  }
}
.Pagination {
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 50%;
  margin-left: auto;
  list-style: none;
}
.Pagination__item {
  display: flex;
  justify-content: center;
  align-items: center;
}
.Pagination__link {
  display: inline-block;
  width: 40px;
  height: auto;
  color: #000;
  font-size: 20px;
  padding: 5px 10px;
  line-height: 30px;
  border-radius: 2px;
  text-align: center;
  text-decoration: none;
}
.Pagination__link:hover {
  background-color: rgba(0,0,0,0.2);
  transition: 0.3s #fff ease;
}
.Pagination__link--disabled,
.Pagination__link:disabled {
  cursor: default;
  opacity: 0.5;
  pointer-events: none;
}
.Pagination__link--nav {
  width: 25px;
  height: 25px;
  background-position: 0 0;
  background-repeat: no-repeat;
  background-size: contain;
}
.Pagination__link--nav:hover {
  background-color: transparent;
}
.Pagination__link--start {
  background-image: url("./assets/first-page-icon.svg");
}
.Pagination__link--end {
  background-image: url("./assets/first-page-icon.svg");
  transform: rotate(180deg);
}
.Pagination__link--previous {
  width: 20px;
  height: 20px;
  background-image: url("./assets/previous-page-icon.svg");
}
.Pagination__link--next {
  width: 20px;
  height: 20px;
  background-image: url("./assets/previous-page-icon.svg");
  transform: rotate(180deg);
}
.Pagination__link--active {
  background-color: #ee6e73;
  color: #fff;
  pointer-events: none;
}
@media (max-width: 1250px) {
  .Pagination {
    width: 100%;
    margin-top: 20px;
  }
  .Pagination__item--form {
    width: 50%;
  }
}
@media (max-width: 700px) {
  .Pagination {
    flex-wrap: wrap;
  }
  .Pagination__item--form {
    width: 100%;
    margin-bottom: 30px;
  }
}
.ItemsForm__list {
  margin: 0;
  padding: 0;
  margin-right: 20px;
  list-style: none;
}
.ItemsForm__item {
  display: flex;
  flex-direction: row;
  align-items: center;
  width: 305px;
  margin: 0;
}
.ItemsForm__label {
  width: 200px;
  marginRight: 10px;
}
.ItemsForm__select {
  margin: 0;
}
@media (max-width: 1250px) {
  .ItemsForm {
    width: 100%;
  }
  .ItemsForm__item {
    width: 100%;
  }
}
@media (max-width: 700px) {
  .ItemsForm__item {
    width: 100%;
  }
  .ItemsForm__list {
    margin-right: 0;
  }
}
.SearchField {
  width: 45%;
  display: flex;
  height: 50px;
}
.SearchField__input {
  height: 100%;
  width: 100%;
  padding-left: 10px;
}
.back-button {
  position: relative;
  display: block;
  padding-left: 30px;
  margin-bottom: 20px;
  text-decoration: none;
  color: #000;
}
.back-button::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  width: 20px;
  background-image: url("./assets/previous-page-icon.svg");
  background-repeat: no-repeat;
  background-position: 0 0;
  background-size: contain;
}
.back-button:hover {
  color: #4d4d4d;
}
.back-button:hover::after {
  background-image: url("./assets/back-button-hover.svg");
}
@media (max-width: 1250px) {
  .SearchField {
    width: 100%;
  }
}
.Spinner {
  display: block;
  position: relative;
  width: 120px;
  height: 120px;
  margin-top: auto;
  margin-bottom: auto;
  margin-left: auto;
  margin-right: auto;
}
.Spinner div {
  box-sizing: border-box;
  display: block;
  position: absolute;
  width: 100px;
  height: 100px;
  margin: 6px;
  border: 6px solid #ee6e73;
  border-radius: 50%;
  animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
  border-color: #ee6e73 transparent transparent transparent;
}
.Spinner div:nth-child(1) {
  animation-delay: -0.45s;
}
.Spinner div:nth-child(2) {
  animation-delay: -0.3s;
}
.Spinner div:nth-child(3) {
  animation-delay: -0.15s;
}
@-moz-keyframes lds-ring {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
@-webkit-keyframes lds-ring {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
@-o-keyframes lds-ring {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
@keyframes lds-ring {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.Table {
  width: 100%;
  margin-top: 10px;
  font-size: 15px;
  border-collapse: collapse;
}
.Table:hover {
  transition: box-shadow 0.25s;
  box-shadow: 0 8px 17px 0 rgba(0,0,0,0.2), 0 6px 20px 0 rgba(0,0,0,0.19);
}
.Table__tr {
  border-bottom: 1px solid #d0d0d0;
}
.Table__th,
.Table__td {
  padding: 15px 5px;
  display: table-cell;
  text-align: left;
  vertical-align: middle;
  border-radius: 2px;
}
.Table__td--description {
  width: 290px;
  padding-left: 30px;
  padding-right: 30px;
}
.Table__td--episodes {
  padding-left: 20px;
}
.Table__th {
  width: 5%;
}
.Table__th--description {
  width: 22%;
  padding-left: 30px;
  padding-right: 30px;
}
.Table__th--genres {
  width: 8%;
}
.Table__th--status {
  width: 5%;
}
.Table__th--title {
  width: 7%;
}
.Table__th--poster {
  width: 15%;
}
.Table__th--number {
  width: 4%;
}
@media (max-width: 900px) {
  .Table {
    font-size: 12px;
  }
}
@media (max-width: 700px) {
  .Table__td--genres,
  .Table__td--description,
  .Table__td--status,
  .Table__td--number {
    display: none;
  }
  .Table__th--genres,
  .Table__th--description,
  .Table__th--status,
  .Table__th--number {
    display: none;
  }
}
